.outroLayout {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 17.5px;
    width: 100%;
    padding: 10px 5% 10px 5%;
    overflow-y: scroll;
    margin: 0% 0 0 0;

}

.ontroText {
    text-align: center;
    width: 90%;
    padding: 10px 0 20px 0;
    display: flex;
    align-items: stretch;
    justify-content: center;
    overflow-y: scroll;
}
